import React, {useState,useEffect} from "react";

import bridge from '@vkontakte/vk-bridge';
import {View,Snackbar,Avatar,ScreenSpinner,SplitLayout
	,Epic,Tabbar,Separator,
	TabbarItem,Panel,PanelHeader,PanelHeaderBack,Group,Placeholder,SplitCol,withAdaptivity,usePlatform,Cell,VKCOM,ViewWidth
} 
from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import { Icon28NewsfeedOutline,Icon28ServicesOutline,Icon28MessageOutline,Icon28ClipOutline,Icon28UserCircleOutline,} from '@vkontakte/icons';
import { Icon28GameCircleFillBlue } from '@vkontakte/icons';
import { Icon28MoreHorizontal } from '@vkontakte/icons';
import { Icon28GraphOutline } from '@vkontakte/icons';
import { Icon36DiamondOutline } from '@vkontakte/icons';
import { Icon36Market } from '@vkontakte/icons';
import {Icon24Error} from '@vkontakte/icons'
import MainPanel from "./Components/MainPanel/MainPanel";
import TopPanel from "./Components/TopPanel";
import ShopPanel from './Components/ShopPanel';
import CasinoPanel from "./Components/CasinoPanel";
import OptionalPanel from './Components/OptionalPanel';
import Itro from './Components/Intro/Intro'


const STORAGE_KEYS = {
  hasSeenIntro:'hasSeenIntro'
}
const ERROR = 'error';
const NAVIGATION = {
  MAIN:'main',
  CASINO:'casino',
  OPTIONAL:'optional',
  TOP:'top',
  SHOP:'shop',
  INTRO:'intro'
}

const App = withAdaptivity(({ viewWidth,users }) => {
  console.log('first render')
  /* Логика VKUI*/
    const platform = usePlatform();
    const [activeStory, setActiveStory] = useState(NAVIGATION['MAIN']);
    const onStoryChange = (e) => {
      setActiveStory(e.currentTarget.dataset.story)}
    const isDesktop = viewWidth >= ViewWidth.TABLET;
    const hasHeader = platform !== VKCOM;

    /* Логика приложения */

	const [user,setUser] = useState(null)
  const [isLoad,setLoad] = useState(false);
  const [snackbar,setSnackbar] = useState(null);
  const [isError,setError] = useState(false);
	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
      const data = await bridge.send('VKWebAppStorageGet', { keys: [STORAGE_KEYS['hasSeenIntro']]});

      console.log(data)
      if(Array.isArray(data['keys'])){
     let ind = data['keys'].findIndex((val) => val['key'] == STORAGE_KEYS['hasSeenIntro']);
     let seen = ind != -1 ? data['keys'][ind]['value'] == 'seen' ? true : false : ERROR;
      
     if(typeof(seen) == "string"  && seen.includes(ERROR)){
       setError(true)
       setLoad(true)
     }
      if(seen){
        setUser(user);
        setLoad(true);
      }
      else if(!seen){
        try {
          await bridge.send('VKWebAppStorageSet', {
            key: STORAGE_KEYS['hasSeenIntro'],
            value: 'seen',
          });
          setActiveStory(NAVIGATION['INTRO']);
          setUser(user);
          setLoad(true);
        } catch (error) {
            setError(true);
            setLoad(true);
            console.error(error)
        }
    }
      }
        console.log('two render');
		}
		fetchData()
	}, []);
  
  if(!isLoad){
    return <ScreenSpinner size="large"/>
  }
  if(isError){
    return (<Snackbar
      layout='vertical'
      onClose={() => setSnackbar(null)}
      before={<Avatar size={24} style={{backgroundColor: 'var(--dynamic_red)'}}><Icon24Error fill='#fff' width={14} height={14} /></Avatar>}
      duration={2000}
    >
     Ошибка, попробуйте перезапустить приложение
    </Snackbar>)
  }
  if(isLoad && !isError)
    return (
      <SplitLayout
        header={hasHeader && <PanelHeader separator={false} />}
        style={{ justifyContent: "center" }}
      >
        <SplitCol
          animate={!isDesktop}
          spaced={isDesktop}
          width={isDesktop ? '560px' : '100%'}
          maxWidth={isDesktop ? '560px' : '100%'}
        >
          <Epic activeStory={activeStory} tabbar={!isDesktop &&
            <Tabbar>
              <TabbarItem
                onClick={onStoryChange}
                selected={activeStory ===  NAVIGATION['MAIN']}
                data-story={NAVIGATION['MAIN']}
                text="Гланая"
              ><Icon36DiamondOutline width={26} height={26}/></TabbarItem>
              <TabbarItem
                onClick={onStoryChange}
                selected={activeStory === NAVIGATION['SHOP']}
                data-story={NAVIGATION['SHOP']}
                text="Магазин"
              ><Icon36Market width={26} height={26}/></TabbarItem>
              <TabbarItem
                onClick={onStoryChange}
                selected={activeStory === NAVIGATION['TOP']}
                data-story={NAVIGATION['TOP']}
                label="12"
                text="Топ"
              ><Icon28GraphOutline height={26} width={26}/></TabbarItem>
              <TabbarItem
                onClick={onStoryChange}
                selected={activeStory === NAVIGATION['CASINO']}
                data-story={NAVIGATION['CASINO']}
                text="Казино"
              ><Icon28GameCircleFillBlue width={26} height={26}/></TabbarItem>
              <TabbarItem
                onClick={onStoryChange}
                selected={activeStory === NAVIGATION['OPTIONAL']}
                data-story={NAVIGATION['OPTIONAL']}
                text="Дополнительно"
              ><Icon28MoreHorizontal height={26} width={26}/></TabbarItem>
            </Tabbar>
          }>
            <View id={NAVIGATION['MAIN']} activePanel={NAVIGATION['MAIN']}>
              <Panel id={NAVIGATION['MAIN']}>
              <MainPanel diamonds={1000}/>
              </Panel> 
            </View>
            <View id={NAVIGATION['SHOP']} activePanel={NAVIGATION['SHOP']}>
              <Panel id={NAVIGATION['SHOP']}>
              <PanelHeader>Магазин</PanelHeader>
                <ShopPanel/>
              </Panel>
            </View>
            <View id={NAVIGATION['TOP']} activePanel={NAVIGATION['TOP']}>
            <Panel id={NAVIGATION['TOP']}>
            <PanelHeader>Топ игроков</PanelHeader>
            <Separator style={{ margin: '12px 0' }} />
                <TopPanel/>
              </Panel>
            </View>
            <View id={NAVIGATION['CASINO']} activePanel={NAVIGATION['CASINO']}>
              <Panel id={NAVIGATION['CASINO']}>
              <CasinoPanel/>
              </Panel>
            </View>
            <View id={NAVIGATION['OPTIONAL']} activePanel={NAVIGATION['OPTIONAL']}>
              <Panel id={NAVIGATION['OPTIONAL']}>
               <OptionalPanel/>
              </Panel>
            </View>
            <View id={NAVIGATION['INTRO']} activePanel={NAVIGATION['INTRO']}>
              <Panel id={NAVIGATION['INTRO']}>
              <PanelHeader>Вступление</PanelHeader>
            <Separator style={{ margin: '12px 0' }} />
                <Itro user={user} setActive={setActiveStory}/>
              </Panel>
            </View>
          </Epic>
        </SplitCol>
      </SplitLayout>
      );}, {
    viewWidth: true
  });
  
export default App

 


	