import React, { Fragment } from 'react';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import './Intro.css'

  

const Intro = ({ user,setActive }) => 
(
	<Fragment>
		<Group>
			<Div style={{display:"flex",flexDirection:"column",alignItems:"center",textAlign:"center"}}>
						{user.photo_200 ? <Avatar src={user.photo_200}/> : null}
						<h2>Привет, {user.first_name}</h2>
						<h3>Это игровой бот, в котором тебе надо добывать алмазы 💎 - это основная игровая валюта.
							Если ты наберёшь больше всех алмазов, то сможешь забрать денежный приз
						</h3>
						<Button mode='commerce' id="bin"  size="xl" level="2" onClick={() => setActive('main')}>
							ОК, всё понятно
						</Button>
					</Div>
				</Group>
	</Fragment>
	
);
export default Intro;
